import React from 'react';
import {Grid, Header, Icon, Form, Segment, Button, Message} from "semantic-ui-react";
import {Link} from 'react-router-dom';
import ExMenu from "./Menu";
import Footer from "./Footer";

import RegisterBackgroundImage from '../static/img/bg8.png';

class Register extends React.Component {
    state = {
        username: '',
        email: '',
        password: '',
        passwordConfirmation: '',
        errors: [],
        loading: false,
        contentHeight: 0
    };

    handleInputError = (errors, inputName) => {
        return errors.some(error => error.message.toLowerCase().includes(inputName)) ? "error" : "";
    };

    handleChange = event => {
        this.setState({[event.target.name]: event.target.value});
    };

    isFormValid = () => {
        let errors = [];
        let error;
        if (this.isFormEmpty(this.state)) {
            error = {message: 'Fill in all fields'};
            this.setState({errors: errors.concat(error)});
            return false;
        } else if (!this.isPasswordValid(this.state)) {
            error = {message: 'Password is invalid'};
            this.setState({errors: errors.concat(error)});
            return false;
        } else {
            return true;
        }
    };

    isFormEmpty = ({username, email, password, passwordConfirmation}) => {
        return !username.length || !email.length || !password.length || !passwordConfirmation.length;
    };

    isPasswordValid = ({password, passwordConfirmation}) => {
        if (password.length < 6 || passwordConfirmation.length < 6) {
            return false;
        } else if (password !== passwordConfirmation) {
            return false;
        } else {
            return true;
        }
    };

    handleSubmit = event => {
        event.preventDefault(); //取消事件的默认动作
        if (this.isFormValid()) {
            this.setState({errors: [], loading: true});
        }
    };

    componentDidMount() {
        let nav = document.getElementById('nav');
        let footer = document.getElementById('footer');
        // let content = document.getElementById('content');

        // console.log("nav=>", nav.clientWidth, nav.clientHeight);
        // console.log("content=>", content.clientWidth, content.clientHeight);
        // console.log("footer=>", footer.clientWidth, footer.clientHeight);
        // console.log("page=>", document.body.clientHeight);

        // content.clientHeight = document.documentElement.clientHeight - nav.clientHeight - footer.clientHeight;
        this.setState({contentHeight: document.documentElement.clientHeight - nav.clientHeight - footer.clientHeight});
    }

    render() {
        const {username, email, password, passwordConfirmation, errors, loading} = this.state;
        return (
            <Grid stackable textAlign='center' verticalAlign="middle">
                <Grid.Row columns={1} style={{paddingBottom: '0px'}}>
                    <Grid.Column id='nav'>
                        <ExMenu/>
                    </Grid.Column>
                </Grid.Row>

                <Grid.Row columns={1}
                          style={{
                              paddingTop: '0px',
                              paddingBottom: '0px',
                              height: this.state.contentHeight,
                              backgroundImage:`url(${RegisterBackgroundImage})`,
                          }}>
                    <Grid.Column style={{maxWidth: 450}} id='content'>
                        <Header as="h1" icon color="orange" textAlign="center">
                            <Icon name="puzzle piece" color="orange"/>
                            Register for CoinLoop
                        </Header>
                        <Form onSubmit={this.handleSubmit} size="large">
                            <Segment stacked>
                                <Form.Input fluid name="username" icon="user" iconPosition="left" placeholder="UserName"
                                            onChange={this.handleChange} value={username} type="text"/>

                                <Form.Input fluid name="email" icon="mail" iconPosition="left"
                                            placeholder="Email Address"
                                            onChange={this.handleChange}
                                            value={email}
                                            className={this.handleInputError(errors, 'email')}
                                            type="email"
                                />

                                <Form.Input fluid name="password" icon="lock" iconPosition="left" placeholder="Password"
                                            onChange={this.handleChange}
                                            value={password}
                                            className={this.handleInputError(errors, 'password')}
                                            type="password"
                                />

                                <Form.Input fluid name="passwordConfirmation" icon="repeat" iconPosition="left"
                                            placeholder="Password Confirmation"
                                            onChange={this.handleChange}
                                            value={passwordConfirmation}
                                            className={this.handleInputError(errors, 'password')}
                                            type="password"
                                />

                                <Button disabled={loading} className={loading ? 'loading' : ''} color="orange" fluid
                                        size="large"> Submit </Button>
                            </Segment>
                        </Form>
                        {
                            errors.length > 0 && (
                                <Message error>
                                    <h3>Error</h3>
                                    {this.displeyErrors(errors)}
                                </Message>
                            )
                        }
                        <Message>Already a user? <Link to="/login">Login</Link> </Message>
                    </Grid.Column>
                </Grid.Row>

                <Grid.Row columns={1}
                          style={{paddingTop: '0px', paddingBottom: '0px'}}>
                    <Grid.Column id='footer'>
                        <Footer/>
                    </Grid.Column>
                </Grid.Row>
            </Grid>
        )
    }
}

export default Register;
